*{
  box-sizing: border-box;
}
body{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
  
}
header{
  padding: 50px;
  background-color: cadetblue;
  text-align: center;

}
nav{
  display: flex;
  flex-direction: row;
  background-color: coral;
}
nav a{
  color: cornsilk;
  padding: 14px 20px;
  text-decoration: none;
}
.row{
  display: flex;
  flex-wrap: wrap;
}
aside{
  flex: 30%;
  background-color: darkgrey;
  padding: 20px;

}
section{
  flex: 70%;
  background-color: darksalmon;
  padding: 20px;

}
footer{
  padding: 20px;
  text-align: center;
  background-color: darkseagreen;
}